.search-wrapper {
    display: flex;
}
.search-wrapper div{
    flex: 3;
    margin-right: 30px;
}
.search-wrapper button{
    flex: 1;
}
.card-body {
    min-height: 150px;
    align-items: center;
    display: flex;
    justify-content: center;
}
.card-text .track-name {
    font-size: 25px;
    color: #212529;
}
.track-name:hover {
    text-decoration: none;
}

@media only screen and (max-width: 768px) {
    .search-wrapper{
        display: block;
    }
    .search-wrapper div {
        margin-right: 0;
    }
}